import React from "react";
import {
  Box,
  Grid,
  Typography,
  Link,
  Container,
  useMediaQuery,
  List,
  ListItem,
} from "@mui/material";
import CustomLine from "../../UI/line/customLine";
import classes from "./footer.module.css";

const Footer = () => {
  const handleAddressClick = () => {
    const mapsUrl = "https://goo.gl/maps/UY3pHFWNcW1hfSCh7";
    window.open(mapsUrl, "_blank");
  };

  const matches = useMediaQuery("(min-width:768px)");

  return (
    <>
      <>
        {matches && (
          <Box
            bgcolor="info.main"
            color="white"
            py={6}
            my={6}
            width="98%"
            mx="1%"
            sx={{ borderRadius: "2rem" }}
          >
            <Container maxWidth="lg">
              <Grid container spacing={6}>
                <Grid item xs={12} sm={6} md={3}>
                  <Typography variant="h6">Hakkımda</Typography>
                  <hr />
                  <Typography variant="body2" textAlign="justify">
                    Yaşam Psikolojik Danışma Merkezi olarak hedefimiz, sizin
                    ihtiyaçlarınıza özel olarak hazırlanmış terapi planları ve
                    etkili çözümler sunarak, sağlıklı bir zihin ve duygusal
                    dengeye ulaşmanızı sağlamaktır.
                  </Typography>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <Typography variant="h6">Navigasyon</Typography>
                  <hr />
                  <Link
                    href="/#/hakkimda"
                    color="inherit"
                    sx={{ textDecoration: "none" }}
                  >
                    <Typography variant="body2">Hakkımda</Typography>
                  </Link>
                  <br />
                  <Link
                    href="/#/hizmetler"
                    color="inherit"
                    sx={{ textDecoration: "none" }}
                  >
                    <Typography variant="body2">Hizmetler</Typography>
                  </Link>
                  <br />
                  <Link
                    href="/#/iletisim"
                    color="inherit"
                    sx={{ textDecoration: "none" }}
                  >
                    <Typography variant="body2">İletişim</Typography>
                  </Link>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <Typography variant="h6">İletişim Bilgileri</Typography>
                  <hr />
                  <Typography variant="body2" textAlign="justify">
                    <span
                      style={{ cursor: "pointer" }}
                      onClick={handleAddressClick}
                    >
                      Adres: Altıntaş Mah. Atatürk Bulvarı No:6 Daire No:112
                      Nazilli/AYDIN
                    </span>
                  </Typography>
                  <br />
                  <Typography textAlign="justify">
                    <Link
                      href="tel:05423822662"
                      underline="none"
                      sx={{ color: "white" }}
                    >
                      Telefon: 0542 382 26 62
                    </Link>
                  </Typography>
                  <br />
                  <Link
                    href="mailto:busracirak04@gmail.com"
                    color="inherit"
                    sx={{ textDecoration: "none" }}
                  >
                    Email: busracirak04@gmail.com
                  </Link>
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <Typography variant="h6">Çalışma Saatleri</Typography>
                  <hr />
                  <Typography variant="body2" textAlign="justify">
                    Pazartesi - Cumartesi: 10:00 - 20:00
                    <br />
                    Pazar: Kapalı
                  </Typography>
                </Grid>
              </Grid>
            </Container>
            <Box mt={5}>
              <Typography variant="body2" align="center">
                ©2023 Psikolojik Danışman Büşra Çırak. Tüm Hakları Saklıdır.
              </Typography>
            </Box>
          </Box>
        )}
      </>
      {!matches && (
        <div
          style={{
            minHeight: "40vh",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
          }}
        >
          <Box mt={2}>
            <Typography
              style={{ userSelect: "none" }}
              variant="body1"
              fontWeight="bold"
              color="textSecondary"
              align="center"
            >
              Çalışma Saatleri
            </Typography>
            <CustomLine width="30%" />
            <List
              sx={{
                position: "absolute",
                display: "inline-block",
                textAlign: "center",
                left: "50%",
                transform: "translateX(-50%)",
              }}
            >
              <ListItem>
                <Box
                  sx={{
                    width: "100%",
                    height: "100%",
                    display: "flex",
                    backgroundColor: "transparent",
                    justifyContent: "space-between",
                    minWidth: "23rem",
                    borderRadius: ".5rem",
                    border: ".06rem solid black",
                  }}
                  className={classes.days}
                  p={1}
                >
                  <Typography
                    sx={{ opacity: ".7" }}
                  >{`Pazartesi > Cuma`}</Typography>
                  <Typography alignContent="flex-end" color="black" ml={3}>
                    {`10.00 > 20.00`}
                  </Typography>
                </Box>
              </ListItem>
              <ListItem>
                <Box
                  sx={{
                    width: "100%",
                    height: "100%",
                    display: "flex",
                    backgroundColor: "transparent",
                    justifyContent: "space-between",
                    borderRadius: ".5rem",
                    border: ".06rem solid black",
                  }}
                  p={1}
                >
                  <Typography sx={{ opacity: ".7" }}>{`Cumartesi`} </Typography>
                  <Typography color="black" ml={3}>
                    {`10.00 > 20.00`}
                  </Typography>
                </Box>
              </ListItem>
              <ListItem>
                <Box
                  sx={{
                    width: "100%",
                    height: "100%",
                    display: "flex",
                    backgroundColor: "transparent",
                    justifyContent: "space-between",
                    borderRadius: ".5rem",
                    border: ".06rem solid black",
                  }}
                  p={1}
                >
                  <Typography sx={{ opacity: ".7" }}>{`Pazar`} </Typography>
                  <Typography color="rgb(209, 83, 83)" ml={3}>
                    {`Kapalı`}
                  </Typography>
                </Box>
              </ListItem>
            </List>
          </Box>
          <Box
            width="100%"
            height="3rem"
            sx={{
              backgroundColor: "#4B778D",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Typography variant="body2">
              Copyright © 2023 Psikolog Büşra Çırak. Tüm Hakları Saklıdır.
            </Typography>
          </Box>
        </div>
      )}
    </>
  );
};

export default Footer;
